import React from 'react';
import { Robot1Geometry } from '../geometries/robot1Geometry';

interface Robot1Props {
  [key: string]: any;
}

/**
 * robot1 component
 * Simple wrapper that uses pre-defined geometry
 * Easy to modify without touching geometry data
 */
export default function Robot1(props: Robot1Props) {
  return <mesh geometry={Robot1Geometry} {...props} />;
}
