import React, { useRef } from 'react';
import type { GroupProps } from '@react-three/fiber';
import Robot1 from './meshes/robot1';
import mainMaterial from './materials/mainMaterial';
import * as THREE from 'three';
interface mainmodel1Props extends GroupProps {
  [key: string]: any;
}

/**
 * mainmodel1 component
 * Main component that assembles all mesh and material parts
 */
export default function Mainmodel1(props: mainmodel1Props) {
  const ref = useRef<THREE.Group>(null);
  
  return (
    <group ref={ref} {...props}>
      <Robot1 material={mainMaterial()} />
    </group>
  );
}
